@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.header-container {
    font-family: 'Playfair Display', serif; 
    text-align: center;
}

.newspaper-name {
    margin-bottom: 0;
    font-size: 3rem;
    font-weight: 900;
}

.header-date {
    margin-top: 0.5rem;
}

.header-category-container {
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.header-category {
    margin: 0.25rem 0rem;
    color: black;
    text-decoration: none;
    font-style: italic;
}

.header-category:focus{
    color: tomato;
}
.header-category:hover{
    color: tomato;
    cursor: pointer;
}

.nav-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.nav-link {
    margin: 0 0.5rem;
    font-weight: 700;
    color: black;
}

.nav-link:focus {
    color: tomato;
}
.nav-link:hover {
    color: tomato;
}



.ownblog-card-container{
    width: 500px;
    padding: 1rem;
    margin: 1.5rem auto;
    border: 1px solid black;
    background-color:#FFFFFF;
}

.ownblog-edit-btn-container{
    display: flex;
    justify-content: space-around;
}

.ownblog-btn {
    width: 200px;
    margin: 1rem 0rem;
}




.blog-card-container {
    width: 500px;
    padding: 1rem;
    height: 400px;
    margin-top: 1.5rem;
    border: 1px solid black;
    overflow: hidden;
    background-color:hsl(0, 0%, 100%);
    color: black;

}

.nav-link-container {
    text-decoration: none;
}

.blog-card-img-container {
    width: 100%;
    height: 250px;
    overflow: hidden;
}

.blog-card-text-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    margin-top: 1rem;
    padding: 0rem 0.5rem;
}

.blog-card-img {
    width: 475px;
    height: 300px;
}

.blog-card-title {
    font-family: 'Playfair Display', serif;
    font-size: 1.5rem;
}

.blog-card-category {
    margin:0;
    font-family: 'Lato', sans-serif;
    text-decoration: none;
}

.blog-card-author {
    font-family: 'Lato', sans-serif;
}
.blog-post-list-container {

}
.aside-container {
    width: 300px; 
    height: 100vh;
    margin-top: 1.5rem;
    border: 1px solid black;
    overflow: hidden;
    padding: 1rem;
    background-color:#FFFFFF;
}

.aside-heading {
    font-family: 'Playfair Display', serif;
    font-weight: 700;
}
.aside-item-container {
    display: flex;
    width: 100%;
    height: 100px;
    margin-top: 1rem;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    background-color:#FFFFFF;
}

.aside-item-img-container {
    display: flex;
    align-items: center;
    width: 100px;
    height: 100px;
    overflow: hidden;
}

.aside-item-img{
    width: 75px;
    height: 75px;
}

.aside-item-text-container {
    padding: 0.5rem 0.5rem;
    text-align: left;
}

.aside-item-category {
    font-size: 11px;
    margin: 0;
    font-family: 'Lato', sans-serif;
}

.aside-item-title {
    font-size: 14px;
    text-align: left;
    
}

.aside-item-author {
    font-size: 11px;
    font-family: 'Lato', sans-serif;
}

.main-container {
    display: flex;
    justify-content: space-around;
    background-color: #f0f0f0;
}


.profile-details-container {
    text-align: left;
    width: 150px;
    margin: 0 auto;
    margin-top: 1rem;
    font-family: "Noto Mono Sans";
}

.profile-details-heading {
    font-size: 1.5rem;
    font-weight: 900;
    color: rgb(231, 105, 83);
    text-align: center;
}

.profile-details-subheading {
    font-size: 1.25rem;
    font-weight: 700;
}

.profile-btn {
    margin: 0rem 0.75rem;
    font-family: "Noto Mono Sans";
    font-weight: 900;
    color: rgb(255, 238, 238)
}

.blog-detail-card-container {
    width: 500px;
    padding: 1rem;
    height: 575px;

    border: 1px solid black;
    overflow: hidden;
    background-color:#FFFFFF;
    margin: 3rem auto;
}
.admin-blog-list-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
